.Container {
    position: relative;
    display: inline-block;
    text-align: center;
}

.Logo {
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    position: absolute;
    width: 150px;
    height: 150px;
    transition: 0.4s ease-out;
}



.LogoHovered {
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    position: absolute;
    width: 180px;
    height: 180px;
    transition: 0.4s;
}

.SkillNameHidden {
    position: absolute;
    left: 50%;
    transform:  translateX(-50%) translateY(450%);
    
    opacity: 0;
    transition: 0.4s;
}

.SkillNameShown {
    position: absolute;
    left: 50%;
    transform:  translateX(-50%) translateY(450%);
    
    opacity: 1;
    transition: 0.4s;
}

@media (max-width: 700px) {
    .Container {
        padding: 25px;
    }

    .Logo {
        width: 100px;
        height: 100px;
    }

    .LogoHovered {
        width: 130px;
        height: 130px;
    }

    .SkillNameHidden {
        transform:  translateX(-50%) translateY(300%);
    }
    
    .SkillNameShown {
        transform:  translateX(-50%) translateY(300%);
    }
}