.tasksList {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(40%, 1fr));
    padding-top: 5%;
    row-gap: 10px;
}

.task {
    display: grid;
    width: 90%;
    grid-template-columns: 10% 1fr;
    column-gap: 10%;
}

.bulbIcon {
    font-size: 3vw;
    color: crimson;
}

.taskText { 
    font-size: 15px;
}

@media only screen and (max-width: 630px) {
    .tasksList {
        grid-template-columns: 1fr;
    }

    .taskText { 
        font-size: 10px;
    }
}