.slider {
    display: inline-flex;
    position: relative;
    align-items: center;
    justify-content: center;
    width: 100%;
    align-content: flex-start;
}

.slide {
    background-color: gainsboro;
    border-radius: 20px;
    width: 90%;
    height: 400px;
    padding: 10px 10%;
    overflow: auto;
}

.slideContent {
    opacity: 0;
}

.slideContent.active {
    opacity: 1;
    transition-duration: 1s;
}

.leftArrow {
    font-size: 105px;
    color: black;
    z-index: 10;
    cursor: pointer;
    user-select: none;
    flex: 0 1 auto;
    display: inline-block;
}

.rightArrow {
    font-size: 105px;
    color: black;
    z-index: 10;
    cursor: pointer;
    user-select: none;
    flex: 0 1 auto;
    display: inline-block;
}