.Container {
    position: relative;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-areas: 
        "React JS JS JS Python"
        ".. HTML .. CSS ..";
    justify-content: center;
    align-items: center;
    height: 550px;
    margin-top: 50px;
}

@media (max-width: 700px) {
    .Container {
        grid-template-columns: 1fr 1fr;
        grid-template-areas: 
        "React Python"
        "JS JS"
        "HTML CSS";
    }
}

.BottomLogos {
    display: flex;
    position: absolute;
    bottom: 0;
}