.home {
    height: 100vh;
    margin-left: 0;
    color: #F28D35;
    background: rgb(255,166,27);
    background: linear-gradient(
        0deg, 
        rgba(255,166,27,1) 2%, 
        rgba(253,106,29,1) 7%, rgba(37,41,176,1) 27%, 
        rgba(2,0,107,1) 44%
    );
    background-size: 100%;
    z-index: 1;
}

.introduction {
    position: absolute; 
    z-Index: 2;
    margin-top: 10vh;
    margin-left: 8vw;
}

.greeting {
    margin: 0;    
    font-size: 2em;
}

.offer {
    margin: 20px 0px 0px;
    font-size: 4em;
}

.feature {
    margin: 50px 0px 0px;
    font-size: 1em;
}

$color: #0cf;

.seemoreButton {
    font-family: inherit;
    font-weight: 700;
    font-size: 1rem;
    text-decoration: none;
    display: inline-block;
    padding: .75rem 1.25rem;
    margin-top: 60px;
    border-radius: 10rem;
    color: #fff;
    text-transform: uppercase;
    letter-spacing: .15rem;
    transition: all .3s;
    position: relative;
    overflow: hidden;
    z-index: 1;
    &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $color;
        border-radius: 10rem;
        z-index: -2;
    }
    &:before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 0%;
        height: 100%;
        background-color: darken($color, 15%);
        transition: all .3s;
        border-radius: 10rem;
        z-index: -1;
    }
    &:hover {
        color: #fff;
        &:before {
            width: 100%;
        }
    }
}