.Layout {
    position: relative;
    font-family: "Montserrat";
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.sections {
    float: left;
    padding: 0vw 10vw;
}

@media (max-width: 480px) {
    .sections {
        padding: 0vw 5vw;
    }
}

/* @media (max-width: 768px) {
    
} */

.section {
    display: flex;
    width: 100vw;
    height: 100vh;
}

.title {
    margin-left: 5vw;
    font-size: 4em;
}

.content {
    margin-left: 7vw;
    font-size: 2em;
    padding-right: 2vw;
}