.background {
    display: inline-flex;
    border-radius: 30px;
    z-index: -1;
    background: linear-gradient(-135deg, 
        #d5007d 10%,
        #e53935 50%,
        #d5007d 100%, 
        #e53935 150%
    );
    background-size: 200% 100%;
    transition: all 0.4s;
}

.background:hover {
    background-position: -70px 0;
    transition: all 0.4s;
}

.button {
    outline: none;
    border: none;
    border-style: none;
    display: inline-table;
    justify-content: center;
    align-items: center;
    min-width: 160px;
    height: 60px;
    font-size: 20px;
    color: white;
    background: transparent;
}

.button:hover {
    pointer-events: auto;
    cursor: pointer;
    left: 0;
}

