.messageArea {
    box-sizing: border-box;
    width: 100%;
    height: 300px;
    min-height: 50px;
    margin-bottom: 17px;
    outline: none;
    border: none;
    resize: vertical;
    padding: 25px 20px 0 20px;
}

.messageArea:placeholder-shown {
    line-height: 0px;
    font-family: Montserrat;
}

.messageArea::placeholder {
    font-size: 15px;
}