.Contact {
    position: relative;
    bottom: 0;
    width: 100%;
    margin-top: 120px;
    background-color: black;
    color: white;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 50px;
}

.title {
    position: relative;
    font-size: clamp(2em, 8vw, 3em);
    text-align: center;
    margin-bottom: 60px;
}

.title::after {
    position: absolute;
    content: '';
    bottom: -30px;
    margin: 0 auto;
    left: 0;
    right: 0;
    width: 2.5rem;
    height: 5px;
    background-color: red;
}

.form {
    width: 40%;
    padding-top: 2%;
}

@media (max-width: 768px) {
    .form {
        width: 60%;
    }
}

@media (max-width: 480px) {
    .form {
        width: 70%;
    }
}