.NavigationItem {
    position: relative;
    color: white;
    text-decoration: none;
    margin-right: 4em;
    transition: (.3s ease-out .1s);
    &::after {
        height: 2px;
        width: 100%;
        background: white;
        content: "";
        position: absolute;
        left: 0px;
        bottom: -2px;
        scale: (0);
        transition: .3s;
    }
    &:hover{
        &::after {
            scale: (1);
            
        }
    }
}
