.section {
    display: inline-block;
    width: 100%;
    margin-top: 120px;
}

.title {
    position: relative;
    font-size: clamp(2em, 8vw, 3em);
    text-align: center;
    margin-bottom: 60px;
}

.title::after {
    position: absolute;
    content: '';
    bottom: -30px;
    margin: 0 auto;
    left: 0;
    right: 0;
    width: 2.5rem;
    height: 5px;
    background-color: dodgerblue;
}

.content {
    margin-left: 7vw;
    margin-right: 7vw;
    font-size: 20px;
}